.intro{
    background-color: white;
    display:flex;
    @media (max-width:1155px) {
    flex-direction: column;
    }
    .left{
        flex:0.5;
        overflow: hidden;
        @media (max-width:1155px) {
            img{
                height: auto;
                width: 50%;
            }
            }
        
    }
    .right{
        flex:0.5;
        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            position: relative;

            h2{
                font-size: 70px;
                margin:  10px 0;   
            }
            h1{
                font-size: 50px;
            }
            h3{
                font-size: 40px;
                font-weight: lighter;
                span{
                    font-weight: bold;
                    color: crimson;
                }    
            }

            @media (max-width:760px) {
                h2{
                    font-size: 50px;
                }
                h1{
                    font-size: 35px;
                }
                h3{
                    font-size: 30px;
                }
                }
                @media (max-width:430px) {
                    h3{
                        font-size: 20px;
                    }
                }
        }
        .wrap2{
            display: flex;
            align-items: center;
            justify-content: center;
            a{
                position: absolute;
                bottom: 50px;
                img{
                    width: 100px;
                    animation: arrowblink 2s infinite;
                }
                @keyframes arrowblink {
                    100%{
                        opacity: 0;
                    }
                }
                @media (max-width:1155px) {
                    img{
                        display: none;
                    }
                    }
                
            }

        }
        
    }
}
