.navbar{
    width: 100%;
    height: 65px;
    background-color: white;
    position: fixed;
    top: 0;
    z-index: 4;
    overflow:hidden;
    margin: 0%;
    transition: all 1s ease;

    .wrapper{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 20px;
        
        .left{

            .logo{
                font-size: 30px;
                text-decoration: none;
                color: crimson;
            }

        }
    }
    .right{
        .hamburger{
            width: 30px;
            height: 20px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            cursor: pointer;
            overflow: hidden;
            transition: all 1s ease;

            span{
                width: 100%;
                height: 3px;
                background-color: crimson;
                transform: orgin;

            }
        }
    }
    &.active{
        background-color: crimson;
        .wrapper{
            .left{
                .logo{
                    color: white;
                }
            }
            .right{
                .hamburger{
                    width: 60px;
                    span{
                        background-color: white;
                        
                        &:first-child{
                            transform: rotate(45deg);

                        }
                        &:nth-child(2){
                            opacity: 0;
                        }
                        &:last-child{
                        
                            transform: rotate(-45deg);
                        }
                    }
                }
            }
        }
    }
}