.Connect {
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  @media (max-width:835px) {
    flex-direction: column;
  }
  .left {
    flex: 0.5;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .wrap {
      h1 {
        font-weight: lighter;
        font-size: 40px;
        margin: 15px;
        span {
          color: crimson;
          font-weight: bold;
          font-size: 60px;
        }
      }
      .imgcontainer{
        display: flex;
          .images{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
          }
          a{
            img{
                width: 100px;
                margin: 20px;
            }
          }
          h3{
            font-weight: lighter;
          }
      }
    }
    .ah{
        text-decoration: none;
        color: black;
    }
    .a-container{
        margin: 20px;
        a{
            text-decoration: none;
            color: black;
            margin: 10px;
            font-size: 17px;
            font-weight: lighter;
            img{
                width: 20px;
                margin-right: 10px;
            }
        }
    }
    @media (max-width:555px) {
      .wrap{
        .h1{
          font-size: 30px;
          span{
            font-size: 50px;
          }
        }
        .imgcontainer{
          .images{
            a{
              img{
                width: 70px;
              }
            }
          }
        }
        .a-container{
          a{
            font-size: 13px;
            img{
              width: 15px;
            }
          }
        }
      }
     }
     @media (max-width:450px) {
      .wrap{
        .h1{
          font-size: 25px;
          span{
            font-size: 40px;
          }
        }
        .imgcontainer{
          .images{
            a{
              img{
                width: 50px;
              }
            }
          }
        }
        .a-container{
          a{
            font-size: 10px;
            img{
              width: 12px;
            }
          }
        }
      }
     }
  }
  .right {
    flex: 0.5;
    img {
      width: 70%;
    }
    @media (max-width:835px) {
     img{
      height: 80%;
      width: auto;
     }
    }
  }
  @media (max-width:1150px) {
    .left{
      flex:0.7;
    }
    .right{
      flex: 0.3;
    }
  }
}
