.nav{
    height: 100vh;

    .sections{
        height: calc(100vh - 60px);
        width: 100vw;
        position: relative;
        top: 60px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        scrollbar-width: none;
        &::-webkit-scrollbar{
            display: none;
        }
        >*{
            height: calc(100vh - 60px);
            width: 100vw;
            scroll-snap-align: start;
        }

    }
}