.menu{
    width: 350px;
    height: 100vh;
    background-color: white;
    position: fixed;
    top: 0;
    right: -350px;
    z-index: 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;
    ul{
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 40px;
        color: crimson;
        width: 60%;
        li{
            margin-bottom: 20px;
           a{
               text-decoration: none;
               color: inherit;
               font-weight: lighter;
              
           }
           &:hover{
               font-weight: bold;
           }
        }
    }

    &.active{
        right: 0;
        background-color: crimson !important;
        ul{
            color: white;
            li{
                color: inherit;
            }
        }
    }
}