.contact{
    overflow: hidden;
    .container{
        .wrapper{
            display: flex;
            overflow: hidden;
            align-items: center;
            @media (max-width:850px) {
                flex-direction: column;
              }
            .left{
                flex:0.5;
                justify-content: center;
                img{
                    width: 75%;
                }
            }
            @media (max-width:850px) {
                .left{
                    img{
                        height: 180px;
                        width: auto;
                    }
                }
              }
            .right{
                flex: 0.5;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                margin: 10px;
                h1{
                    font-size: 30px;
                    font-weight: lighter;
                }

                form{
                    flex-direction: column;
                    align-items: center;
                    justify-content: space-around;
                    
                    
                    input{
                        width: 400px;
                        height: 45px;
                        font-size: 15px;
                        margin: 10px 0px;
                    }
                    textarea{
                        width: 400px;
                        height: 180px;
                        font-size: 15px;
                        margin: 10px 0px;
                    }
                    button{
                        width: 400px;
                        height: 45px;
                        color: white;
                        background-color: crimson;
                        border: none;
                        border-radius: 10px;
                        font-size: larger;
                        cursor: pointer;
                        
                    }
                    span{
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        margin-top: 20px;
                        font-size: 20px;
                        color: crimson;
                    }
                }
            }
            @media (max-width:435px) {
                .right{
                    h1{
                        font-size: 20px;
                    }
                    form{
                        input{
                            width: 300px;
                            height: 35px;
                            font-size: 12px;
                        }
                        textarea{
                            width: 300px;
                            height: 35px;
                            font-size: 12px;
                        }
                        button{
                            width: 300px;
                            height: 35px;
                        }
                    }
                }
              }

        }

    }
    footer{
        bottom: 10px;
        margin-top: 10px;
        font-size: 20px;
        font-weight: lighter;
        display: flex;
        align-items: center;
        justify-content: center;
        color: crimson;
    }
}