.Details {
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  @media (max-width:1000px) {
    flex-direction: column;
  }
  .left {
    flex: 0.5;
    overflow: hidden;
    h3 {
      margin: 10px;
      margin-left: 15px;
      font-size: 40px;
      font-weight: lighter;
      span {
        font-size: 50px;
        font-weight: bold;
        color: crimson;
      }
      
    }
    h4 {
      margin: 10px;
      margin-left: 15px;
      font-size: 20px;
      font-weight: lighter;
    }

    .button {
      border-radius: 4px;
      background-color: crimson;
      border: none;
      color: #ffffff;
      text-align: center;
      font-size: 20px;
      font-weight: lighter;
      padding: 5px;
      width: 190px;
      transition: all 0.5s;
      cursor: pointer;
      margin: 5px;
      margin-left: 15px;
    }

    .button span {
      cursor: pointer;
      display: inline-block;
      position: relative;
      transition: 0.5s;
    }

    .button span:after {
      content: "\00bb";
      position: absolute;
      opacity: 0;
      top: 0;
      right: -20px;
      transition: 0.5s;
    }

    .button:hover span {
      padding-right: 25px;
    }

    .button:hover span:after {
      opacity: 1;
      right: 0;
    }
    @media (max-width:1000px) {
      h3{
        font-size: 30px;
        span{
          font-size: 40;
        }
      }
      h4{
        font-size: 15px;
      }
      .button{
        font-size: 15px;
      }
    }

    @media (max-width:423px) {
      h3{
        font-size: 25px;
        span{
          font-size: 30px;
        }
      }
      h4{
        font-size: 13px;
      }
      .button{
        font-size: 13px;
        width:150px;
      }
    }
  }
  .right {
    flex: 0.5;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
    }
  }
  @media (max-width:571px) {
    .left{
      flex:0.7;
    }
    .right{
      flex: 0.3;
    }
  }
}
