.tech {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  overflow: hidden;

  h1 {
    font-weight: lighter;
    margin: 10px;
    font-size: 35px;
    height: auto;

    @media (max-width:700px) {
      font-size: 20px;
    }
    @media (max-width:500px) {
      font-size: 15px;
    }
  }
  .imgcntwrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    @media (max-width:700px) {
      flex-direction: row;
    }

    .imgcontainer {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 100px;
      overflow: hidden;
      @media (max-width:700px) {
        flex-direction: column;
        justify-content: space-between;
        margin-bottom: 20px;
      }

      .images {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        overflow: hidden;


        img {
          width: 130px;
          max-height: 130px;
          height: auto;
          margin: 20px;
          z-index: 2;
          transition: all 0.8s ease;
          overflow: hidden;
        }
        @media (max-width:1175px) {
          img{
            height: 60px;
            width: auto;
          }
        }
        @media (max-width:800px) {
          img{
            height: 50px;
            width: auto;
          }
        }
        @media (max-width:700px) {
          img{
            height: 50px;
            width: auto;
          }
        }
        h3 {
          position: absolute;
          font-size: 35px;
          font-weight: lighter;
          opacity: 0;
          color: crimson;
          transition: all 0.8s ease;
          overflow: hidden;
        }
        @media (max-width:1175px) {
          h3{
            font-size: 25px;
          }
        }
        &:hover {
          img {
            opacity: 0;
          }
          h3 {
            opacity: 1;
          }
        }
      }
    }
  }
}
